import React, { useContext, useState } from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { FormattedMessage, navigate, Link, useIntl } from 'gatsby-plugin-intl';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Query, AllorsSerialisedItem, AllorsUnifiedGood } from '../../graphql-types';
import { StoreContext } from '../store/Store';
import { Separator } from '../components/separator';
import { useToasts } from 'react-toast-notifications';
import axios from 'axios';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Breadcrumb from 'react-bootstrap/Breadcrumb';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import Table from 'react-bootstrap/Table';

import PhoneInput, { isValidPhoneNumber } from 'react-phone-number-input';
import 'react-phone-number-input/style.css';

const RequestPage: React.FunctionComponent<{ data: Query }> = ({
  data: {
    site: {
      siteMetadata: { allorsApiUrl },
    },
    allAllorsUnifiedGood,
    allAllorsSerialisedItem,
  },
}) => {
  const {
    email: [email, setEmail],
    telephone: [telephone, setTelephone],
    comment: [comment, setComment],
    productIds: [productIds, setProductIds],
    serialisedItemIds: [serialisedItemIds, setSerialisedItemIds],
    removeProductId,
    removeSerialisedItemId,
  } = useContext(StoreContext);

  const [formClass, setFormClass] = useState('');

  const products = allAllorsUnifiedGood.nodes.filter((v) => productIds.indexOf(v.allorsId) !== -1);
  const serialisedItems = allAllorsSerialisedItem.nodes.filter((v) => serialisedItemIds.indexOf(v.allorsId) !== -1);

  const count = products.length + serialisedItems.length;

  const { addToast } = useToasts();

  const intl = useIntl();

  const removeProduct = (product: AllorsUnifiedGood) => {
    removeProductId(product.allorsId);
    addToast(`Successfully removed ${product.name}`, {
      appearance: 'info',
      autoDismiss: true,
    });
  };

  const removeSerialisedItem = (serialisedItem: AllorsSerialisedItem) => {
    removeSerialisedItemId(serialisedItem.allorsId);
    addToast(`Successfully removed ${serialisedItem.name}`, {
      appearance: 'info',
      autoDismiss: true,
    });
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const form = event.target as HTMLFormElement;
    const emailField = form.elements.namedItem('email') as HTMLFormElement;
    const telephoneField = form.elements.namedItem('telephone') as HTMLFormElement;

    const isEmailValid = emailField.checkValidity();
    const isTelephoneValid = isValidPhoneNumber(telephone);
    telephoneField.setCustomValidity(!isTelephoneValid ? 'invalid' : '');

    if (!isEmailValid || !isTelephoneValid) {
      setFormClass('was-validated');
      return;
    }

    setFormClass('');

    const model = {
      email,
      telephone,
      comment,
      productIds,
      serialisedItemIds,
    };
    axios
      .post(`${allorsApiUrl}/request`, model)
      .then(() => {
        setProductIds([]);
        setSerialisedItemIds([]);
        addToast('Successfully saved', {
          appearance: 'success',
          autoDismiss: true,
        });
        navigate('/');
      })
      .catch((error) => {
        if (error.response?.status === 403) {
          const messages = error.response.data as string[];
          for (const message of messages) {
            addToast(`${message}`, {
              appearance: 'error',
              autoDismiss: true,
            });
          }
        } else {
          addToast(`${error}`, {
            appearance: 'error',
            autoDismiss: true,
          });
        }
      });
  };

  return (
    <Layout>
      <SEO title={intl.formatMessage({ id: 'request' })} description={intl.formatMessage({ id: 'request-description' })} />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <Breadcrumb>
                    <Breadcrumb.Item>
                      <Link to="/">Home</Link>
                    </Breadcrumb.Item>
                    <Breadcrumb.Item active>
                      <FormattedMessage id="request-a-quote" />
                    </Breadcrumb.Item>
                  </Breadcrumb>
                  <h1 className="page-title">
                    <FormattedMessage id="request-a-quote" />
                  </h1>
                  <p className="page-description">SEO Friendly description </p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>

          <Form onSubmit={handleSubmit} noValidate className={formClass}>
            <Container>
              <Row>
                <Col xs={{ span: 12, order: 1 }} md={{ span: 6, order: 0 }}>
                  <Form.Group>
                    <Form.Label>
                      <FormattedMessage id="email" /> *
                    </Form.Label>
                    <Form.Control
                      id="email"
                      type="email"
                      required
                      value={email}
                      onChange={(e) => {
                        setEmail(e.target.value);
                      }}
                    />
                  </Form.Group>
                  <Form.Group>
                    <Form.Label>
                      <FormattedMessage id="telephone" /> *
                    </Form.Label>
                    <PhoneInput
                      id="telephone"
                      numberInputProps={{ className: 'form-control' }}
                      value={telephone ?? ''}
                      onChange={(v) => setTelephone(v)}
                    />
                    <div className="invalid-feedback">Please provide a valid telephone number.</div>
                  </Form.Group>
                  <Form.Group>
                    <Form.Label>
                      <FormattedMessage id="comment" />
                    </Form.Label>
                    <Form.Control
                      id="comment"
                      as="textarea"
                      value={comment}
                      onChange={(e) => {
                        setComment(e.currentTarget.value);
                      }}
                    />
                  </Form.Group>
                  <Button type="submit" color="primary">
                    <FormattedMessage id="send" />
                  </Button>
                  <Link to="/catalogue"> or add more products</Link>
                </Col>
                <Col xs={{ span: 12, order: 0 }} md={{ span: 6, order: 0 }} className="pt-3">
                  {count === 0 && (
                    <Alert>
                      No products selected. Check out our <Link to="/Catalogue">Catalogue</Link> or <a href="/Brands">Brands</a> to add
                      products.
                    </Alert>
                  )}
                  {count > 0 && (
                    <Table striped hover>
                      <thead>
                        <tr>
                          <th scope="col" style={{ width: '25%' }}></th>
                          <th scope="col">Code</th>
                          <th scope="col">Name</th>
                          <th scope="col"></th>
                        </tr>
                      </thead>
                      <tbody>
                        {products.map((product) => {
                          return (
                            <tr key={product.slug}>
                              <td>
                                <Img fluid={product.primaryPhoto.file.childImageSharp.fluid} alt="Product"></Img>
                              </td>
                              <td></td>
                              <td>{product.name}</td>
                              <td>
                                <Button type="button" color="danger" onClick={() => removeProduct(product)}>
                                  Remove
                                </Button>
                              </td>
                            </tr>
                          );
                        })}
                        {serialisedItems.map((serialisedItem) => {
                          return (
                            <tr key={serialisedItem.allorsId}>
                              <td>
                                <Img fluid={serialisedItem.primaryPhoto.file.childImageSharp.fluid} alt="Product"></Img>
                              </td>
                              <td>{serialisedItem.itemNumber}</td>
                              <td>{serialisedItem.name}</td>
                              <td>
                                <Button type="button" color="danger" onClick={() => removeSerialisedItem(serialisedItem)}>
                                  Remove
                                </Button>
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </Table>
                  )}
                </Col>
              </Row>
            </Container>
          </Form>
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query {
    site {
      siteMetadata {
        allorsApiUrl
      }
    }
    allAllorsUnifiedGood {
      nodes {
        allorsId
        name
        primaryPhoto {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
    allAllorsSerialisedItem {
      nodes {
        allorsId
        itemNumber
        name
        primaryPhoto {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  }
`;

export default RequestPage;
